

.navbar {
    position: sticky;
    top: 0;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* background-color: #f8f8f8; */
    background-color: rgb(0, 0, 0);
    padding: 10px 20px;
    border-bottom: 1px solid #4c4c4c;
    margin-bottom: 20px;
}

.navbar-left .logo {
    height: 70px;
    width: 150px;
    /* margin-left: 30px; */
}

.navbar-center {
    flex: 1;
    display: flex;
    justify-content: center;
}

.nav-links {
    display: flex;
    gap: 60px;
    list-style: none;
}

.nav-links li a {
    text-decoration: none;
    color: #dedddd;
    /* color: #333; */
    font-weight: bold;
    font-size: 16px;
}

.nav-links li a:hover {
    color: #FF0000;
}

.navbar-right {
    display: flex;
    align-items: center;
    gap: 20px;
}

.search-bar {
    position: relative;
}

.search-bar input {
    padding: 8px 15px;
    width: 200px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.search-button {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    background-color: transparent;
    border: none;
    font-size: 18px;
    cursor: pointer;
}

.icons .icon {
    font-size: 20px;
    cursor: pointer;
}

.icons .icon:hover {
    color: #888;
}